#root{
  height:100%;
  width:100%;
}
.logo_div{
  padding-top: 9% !important;
}
body {
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Encode Sans','Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color:white
}
hr.my-5 {
	border: none;
}
h1 {
	font-size: 30px;
}
h2 {
	font-size: 70px;
	margin-top: 170px;
}

.cap{
  text-transform: capitalize;
}
.App{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo_div{
  text-align:center;
}
.logo{
  height:5rem;
}

.trumba_frame, .trumba_frame iframe{
  position: fixed;
  bottom:10%;
  margin: 0% 10% 0% 10%;
  width: 80%;
}
.hr{
  border-color:transparent;
}
.trumba_frame,
.trumba_frame td {
	border: none
}
.trumba_frame td {
	width: 16%;
	padding: 20px;
	vertical-align: middle;
}
.trumba_frame span.time {
	text-transform: uppercase;
	font-size: 20px;
}
.trumba_frame span.event {
	font-weight: bold;
	font-size: 40px;
	line-height: 45px;
}
.trumba_frame span.location {
	display:block;
	margin-top: 5px;
	color: #aaaaaa;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

